@import url("https://fonts.googleapis.com/css2?family=Kantumruy+Pro&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font2);
}
:root {
  --font1: "Kantumruy Pro";
  --font2: "Poppins";
}
.h1__main {
  font-family: var(--font1) !important;
  font-size: 30px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 20px;
  display: inline-block;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.wrapper {
  overflow: hidden;
  position: relative;
  display: inline-flex;
  flex-direction: column;
  margin: 50px auto;
  padding: 50px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
  background-color: #fff;
}
body {
  background-color: #f5eddc;
}
.input-wrapper {
  padding: 20px 20px 20px 0;
  display: inline-flex;
  flex-direction: column;
}
.input-wrapper p {
  display: inline-block;
  padding: 5px 0;
  border-radius: 5px;
  font-size: 17px;
  margin-bottom: 15px;
}
.input-wrapper p span {
  color: red;
}
.checkbox-wrapper {
  display: flex;
  align-items: center;
  margin: 25px 0 0 0;
}
.checkbox-wrapper input {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}
.input-wrapper__input {
  height: 40px;
  width: 350px;
  border-radius: 5px;
  border: 1px solid #b3b3b3;
  padding: 10px;
  outline-color: skyblue;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  font-family: var(--font2);
  font-size: 17px;
}
.btn {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  border: none;
  margin-top: 20px;
  background-color: #53bf9d;
  color: #fff;
  font-size: 19px;
  font-family: var(--font2);
  cursor: pointer;
}

.alert__p {
  font-family: var(--font2);
  font-size: 17px;
  padding: 20px;
  color: #fff;
}
.custom-modal {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: rgba(0, 0, 0, 0.8);
  justify-content: center;
  align-items: center;
}
.yt-modal {
  width: 60%;
  max-width: 900px;
}
.meta-info {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  color: #191919;
  font-size: 15px;
  padding: 5px 0;
  text-align: center;
  background-color: #53bf9d;
  color: #fff;
  font-weight: bold;
}
@media (max-width: 540px) {
  .wrapper {
    width: 90%;
    margin: 20px auto 50px auto;
    padding: 10px 10px 50px 10px;
  }
  .yt-modal {
    width: 95%;
  }
  .input-wrapper__input {
    width: 100%;
  }
}
